.inputs {
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
}

label {
    display: flex;
    flex-direction: column;
    position: relative;
}

label:focus-within {
    color:#2962FF;
}

input,
textarea {
    max-width: 200px;
    padding-top: 18px;
    padding-bottom: 18px;
    padding-left: 12px;
    border-radius: 8px;
    border: 1px solid #828282;
    outline: none;
}

input + small {
    color: #828282;
}

input:hover {
    border: 1px solid #333333;
}

input:focus {
    border: 1px solid #2962FF;
}

/*///////////////////ERROR//////////////// */

.error {
    border: 1px solid #D32F2F
}

.error:hover {
    border: 1px solid #333333;
}

.error:focus {
    border: 1px solid #D32F2F
}

/* ///////////////// DISABLED ////////////////////////// */

input:disabled {
    background: #F2F2F2;
    border: 1px solid #E0E0E0;
    cursor: not-allowed;
}
/* /////////////////// WITH SOME INTERESTING TEXT ///////////////////////////////// */

.errorText,
.errorText:hover,
.errorText:focus {
    border: 1px solid #D32F2F
}

.errorText + small {
    color: #D32F2F;
}

